import * as React from 'react'
import Icon from "../images/project-link.svg";

function language(l) {
    
    if(l === undefined) return undefined;

    switch(l){
        case 'c++': return 'cpp';
        case 'c#' : return 'cs';
        default: return l;
    }
}


const ProjectCard = ({projectTitle, year, most_used_language, description, url}) => {

    return (
        <div className="col">
          <div className="card h-100">
            <div className="card-body">

              <div className='d-flex justify-content-between mb-4'>
                <span>{year}</span>
                <span className={`badge my-${language(most_used_language?.toLowerCase())}-badge`}>{most_used_language}</span>
              </div>

              <h5 className="card-title"><b>{projectTitle}</b></h5>
              <p className="card-text">{description}</p>
            </div>
            <div className="card-footer">
              <a aria-label='access site' href={url} className="btn btn-primary my-second-button prog-button align-content-center" type="button">
                <div style={{display: 'flex', alignItems: 'center', height: 100 + '%'}}>
                  <div style={{marginRight: 10 + "px"}}>Page</div><Icon/></div></a>
            </div>
          </div>
        </div>
    )

}

export default ProjectCard